<template>
  <div>
    <h1></h1>
    <Form @submit.native.prevent="checkForm" v-if="!$store.state.login.loginStatus">
      <FormItem></FormItem>
      <FormItem>
        <i-input v-model="user" placeholder="" required clearable></i-input>
      </FormItem>
      <FormItem>
        <Input v-model="password" type="password" placeholder="" required
          clearable />
      </FormItem>
      <FormItem>
        <Button html-type="submit" type="primary" :disabled="$store.state.login.signing">
          {{$store.state.login.signing?'...':''}}
        </Button>
      </FormItem>
    </Form>
    <div v-else>
      <h1> {{$store.state.login.user}}</h1>
      <Button type="primary" @click="$store.dispatch('submitLogout')"></Button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DrawerLogin',
  components: {},
  data: function() {
    return {
      user: '',
      password: ''
    };
  },
  methods: {
    checkForm: function() {
      this.$store.commit('updateSigning');
      this.$store
        .dispatch({
          type: 'submitLogin',
          user: this.user,
          password: this.password
        })
        .then(() => {
          const destinationPage = this.$store.state.login.destinationPage;
          if (destinationPage) {
            this.$router.push(destinationPage);
            this.$store.commit('recordDestinationPage', '');
            this.$store.commit('closeDrawerPage', 'login');
          }
        });
    }
  }
};
</script>

<style lang="less" scoped>
form {
  width: 50%;
}
</style>
